import swal from "sweetalert";
import Customs from "./customService";


export default {
    name: 'SocietyPersonal',
    props: {
        isForm: Boolean
    },
    data: () => ({
        userId: "",
        //newSocietyDescription: "",
        listSocieties: [],
        selectedId : "",
        jobs: [{text:'Dirigeant', value: 0}, {text:'Employé', value: 1}],
    }),

    methods: {
        async getMySocieties() {
            try {
                let user = Customs.getUserDetails();
                this.userId = user._id;        
                await this.$http
                .post("/societyUser/byUser/", {
                    userId: this.userId
                })
                .then(response => {
                    this.listSocieties = response.data.linkEmployees;
                })
                .catch(error => console.log(error));
            }
            catch(err) {
                let error = err.response;
                if (error.status == 409) {
                    swal("Error", error.data.message, "error");
                } else {
                    swal("Error", error.data.err.message, "error");
                }
            }
        },
        /*getSociety(id) {
            this.selectedId = id;
        }*/
    },

    created() {
        this.getMySocieties();
    },

    updated() {
    },

    mounted() {

    }
};